<template>
    <div>
        <ul>
            <li v-for="item in allColor" :key="item.id" :style="{backgroundColor:item}">{{allColor}}</li>
        </ul>
    </div>
</template>
<script>
    import {allColor} from '@/color.js'
    export default{
        data(){
            return {
                allColor
            }
        }
    }
</script>
<style>
    li{
        width:100%;
        height:5%;
        list-style:none;
    }
</style>




